import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { AddSeriePage } from '../pages/add-serie/add-serie';
import { ViewSeriePage } from '../pages/view-serie/view-serie';
import { EditSeriePage } from '../pages/edit-serie/edit-serie';
import { SerieServiceProvider } from '../providers/serie-service/serie-service';
import { IonicStorageModule } from '@ionic/storage';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@NgModule({
  declarations: [MyApp, HomePage, AddSeriePage, ViewSeriePage, EditSeriePage],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      monthNames: [
        "janvier",
        "février",
        "mars",
        "avril",
        "mai",
        "juin",
        "juillet",
        "août",
        "septembre",
        "octobre",
        "novembre",
        "décembre"
      ]
    }),
    IonicStorageModule.forRoot(),
    FormsModule,
    ReactiveFormsModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    AddSeriePage,
    ViewSeriePage,
    EditSeriePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    SerieServiceProvider
  ]
})
export class AppModule {}



