import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { SerieServiceProvider } from "../../providers/serie-service/serie-service";
import { Serie } from "../../models/serie.model";
import { FormControl, FormGroup } from "@angular/forms";
import moment from "moment";

@IonicPage()
@Component({
  selector: "page-edit-serie",
  templateUrl: "edit-serie.html"
})
export class EditSeriePage {
  formGroup: FormGroup;
  serie: Serie;
  title: string;
  status: any;
  nbSaison: any;
  lastSee: any;
  inProgress: any;
  content: string;
  creaDate: string;
  creaDateText: string;
  lastDate: string;
  lastDateText: string;
  saisons: any;
  saisonSees: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private serieService: SerieServiceProvider
  ) {
    this.serie = this.navParams.get("serie");

    this.title = this.serie.title;
    this.nbSaison = this.serie.nbSaison;
    this.lastSee = this.serie.lastSee;
    this.inProgress = this.serie.inProgress;
    this.status = this.serie.status;
    this.content = this.serie.content;
    this.creaDate = this.serie.creaDate;
    this.creaDateText = this.serie.creaDateText;

    this.lastDate = moment().format("YYYY-MM-DD");
    this.lastDateText = "Mise à jour :";

    this.formGroup = new FormGroup({
      title: new FormControl(),
      nbSaison: new FormControl(),
      lastSee: new FormControl(),
      inProgress: new FormControl(),
      status: new FormControl(),
      content: new FormControl(),
      creaDate: new FormControl(),
      creaDateText: new FormControl(),
      lastDate: new FormControl(),
      lastDateText: new FormControl()
    });
  }
  ionViewDidLoad() {
    this.sendValue(this.nbSaison);
    this.sendVal(this.lastSee, this.inProgress);
    this.sendStat();
  }
  sendValue(nbSaison) {
    let n = parseInt(this.nbSaison) + 1;
    let i;
    this.saisons = ["Aucune"];
    for (i = 1; i < n; i++) {
      this.saisons.push("Saison " + i);
    }
  }
  sendVal(lastSee, inProgress) {
    let n = parseInt(this.nbSaison) + 1;
    let i = this.saisons.indexOf(lastSee) + 1;
    this.saisonSees = ["Aucune"];
    if (i < 1) {
      i = 1;
    }
    if (parseInt(this.nbSaison) === this.saisons.indexOf(this.lastSee)) {
      this.inProgress = ["Aucune"];
    } else {
      if (lastSee === this.inProgress) {
        this.inProgress = ["Aucune"];
      } else {
        for (i ; i < n; i++) {
          this.saisonSees.push("Saison " + i);
        }
      }
    }
  }
  sendStat() {
    if (('Saison ' + this.nbSaison) === (this.lastSee)) {
      this.status = "- terminée -";
    } else {
      if (this.inProgress == "Aucune") {
        this.status = "- à voir -";
      } else {
        if (this.inProgress !== "Aucune"){
          this.status = "- en cours -";
        }
      }
    }
  }
  deleteSerie(createDate: number) {
    this.serieService.deleteSerie(createDate);
  }
  saveSerie(serie: Serie) {
    this.serieService.saveSerie(serie);
    this.serieService.upAlert();
    this.navCtrl.popToRoot();
  }
}



