import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AddSeriePage } from '../add-serie/add-serie';
import { SerieServiceProvider } from '../../providers/serie-service/serie-service';
import { Serie } from '../../models/serie.model';
import { ViewSeriePage } from '../view-serie/view-serie';
import { AlertController } from "ionic-angular";

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomePage {
  series: Promise<Serie[]>;
  serie: Serie;
  swipe: boolean = false;

  constructor(
    public navCtrl: NavController,
    private serieService: SerieServiceProvider, private alertCtrl: AlertController
  ) {}
  ionViewWillEnter() {
    this.series = this.getAllSeries();
  }
  addSerie() {
    this.navCtrl.push(AddSeriePage);
  }
  getSerie(createDate: number) {
    this.serieService.getSerie(createDate).then(n => {
      this.serie = n;
      this.navCtrl.push(ViewSeriePage, { serie: this.serie });
    });
  }
  getAllSeries() {
    return this.serieService.getAllSeries();
  }
  reorderItems(indexes) {
    return this.serieService.reorderItems(indexes);
  }
  swipeEvent(e) {
    if (this.swipe == false) {
      return (this.swipe = true);
    } else {
      return (this.swipe = false);
    }
  }
  infoAlert() {
  let alert = this.alertCtrl.create({
    title: 'infos Séries TV apps',
    subTitle: 'Version : 2.1.1',
    message: 'Contacter le développeur : simonberoudblanc.fr',
    buttons: ['Retour']
  });
  alert.present();
  }
}



