import { Serie } from '../../models/serie.model';
import { SerieServiceProvider } from '../../providers/serie-service/serie-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EditSeriePage } from '../edit-serie/edit-serie';
import { AlertController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: "page-view-serie",
  templateUrl: "view-serie.html"
})
export class ViewSeriePage {
  serie: Serie;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private serieService: SerieServiceProvider,
    private alertCtrl: AlertController
  ) {
    this.serie = this.navParams.get("serie");
  }
  deleteSerie(createDate: number) {
    this.serieService.deleteSerie(createDate);
    this.navCtrl.pop();
  }
  getSerie(createDate: number) {
    this.serieService.getSerie(createDate).then(n => {
      this.serie = n;
      this.navCtrl.push(EditSeriePage, { serie: this.serie });
    });
  }
  deleteSerieConfirm(createDate: number) {
    let alert = this.alertCtrl.create({
      title: "Supprimer la série ?",
      buttons: [
        {
          text: "Annuler",
          role: "cancel",
          handler: () => {}
        },
        {
          text: "Confirmer",
          handler: () => {
            this.serieService.deleteSerie(createDate);
            this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }
}



