import { Injectable } from "@angular/core";
import { Storage } from "@ionic/storage";
import { Serie } from "../../models/serie.model";
import { AlertController } from "ionic-angular";

@Injectable()
export class SerieServiceProvider {
  private series: Serie[] = [];
  private serie: Serie;

  constructor(public storage: Storage, private alertCtrl: AlertController) {}

  saveSerie(serie: Serie) {
    serie.createDate = Date.now();
    this.series.unshift(serie);
    this.storage.set("series", this.series);
  }
  getAllSeries() {
    return this.storage.get("series").then(series => {
      this.series = series == null ? [] : series;
      return this.series;
    });
  }
  getSerie(createDate: number) {
    return this.storage.get("series").then(series => {
      this.serie = [...series].find(r => r.createDate === createDate);
      return this.serie;
    });
  }
  deleteSerie(createDate: number) {
    this.series = this.series.filter(serie => {
      return serie.createDate !== createDate;
    });
    this.storage.set("series", this.series);
  }
  upAlert() {
    let alert = this.alertCtrl.create({
      title: "Mise à jour réussie !",
      buttons: ["Ok"]
    });
    alert.present();
  }
  reorderItems(indexes) {
    let element = this.series[indexes.from];
    this.series.splice(indexes.from, 1);
    this.series.splice(indexes.to, 0, element);
  }
}



